export function withName(
  list: [string, React.ComponentType<AntdFormMiddlewareProps>][]
): React.FC<AntdFormMiddlewareProps>[] {
  return list.map(([name, Component]) => {
    const ComponentWithName: React.FC<AntdFormMiddlewareProps> = (props: AntdFormMiddlewareProps) => {
      if (get(props.extraProps, 'component') !== name) return props.next(props);
      return component(props);
    };
    return ComponentWithName;
  });
}

export const preset = [
  // SubmitButtonMw,
  // ValidateMw,
  SubmitButtonWithValidationMw,

  ExtraPropsMw,
  ColMw,
  FieldsetTemplateMw,
  RowMw,
  ...schemaMws,
  FormItemTemplateMw,

  ...withName([
    ['DatePicker', DatePickerMw],
    ['Rate', RateMw],
    ['Switch', SwitchMw],
    ['TextArea', TextAreaMw],
    ['Password', PasswordMw],
    ['RadioGroup', RadioGroupMw],
  ]),

  CheckboxGroupMw,
  CheckboxMw,
  SelectMw,
  InputMw,
  InputNumberMw,
  NotSupportedMw,
];

export default preset;
